@import 'colors';



/* ### Start of page index.html ### */
body {
	background: #fff; }

.full-container {
	width: 100%;
	max-width: 100%;
	margin: 0 auto;
	.row {
		.twelve {
			.title {
				#map {
					height: 600px; } } } } }

#header {
	margin-top: 50px;
	#main-header {
		#logo {
			float: left;
			a#name {
				&:hover {
					text-decoration: none; } } }
		#name {
			font-size: 2rem;
			font-weight: bold;
			color: $links-color-hover; }
		label, input {
			display: none; }
		nav#main-menu {
			float: right;
			line-height: 2;
			ul {
				padding: 0;
				margin: 0;
				li {
					display: inline-block;
					list-style: none;
					margin: 0 0 0 20px;
					a {
						text-transform: uppercase;
						text-decoration: none; }
					.link-color {
						color: $links-color;
						&:hover {
							color: $links-color-hover; } }
					#active {
						color: $links-color-hover; } } } } } }


#main {
	.title {
		margin: 100px 0;
		.lead {
			span {
				font-weight: bold; } } }
	.about-me {
		margin-bottom: 7rem;
		.row {
			.twelve {
				.photo-of-me {
					img {
						display: block;
						width: 100%;
						height: auto; } }
				.about-me-lead {
					font-weight: bold;
					margin-top: 3rem; } } } }
	.work {
		.grid {
			display: grid;
			height: 100%;
			grid-template-rows: auto;
			grid-template-columns: repeat(6, 1fr);
			grid-gap: 20px;
			align-items: center;
			.image1 {
				grid-row: 1;
				grid-column: 1 / 4;
				overflow: hidden; }
			.image2 {
				grid-row: 1;
				grid-column: 4 / 7;
				overflow: hidden; }
			.image3 {
				grid-row: 2;
				grid-column: 1 / 7;
				overflow: hidden; }
			img {
				display: block;
				width: 100%;
				height: auto;
				filter: grayscale(90%);
				transition: all ease-in-out 300ms;
				&:hover {
					filter: grayscale(0%);
					cursor: pointer;
					transform: scale(1.05); } } } } }


#heads-up {
	.container {
		.row {
			.twelve {
				.contact-me {
					padding: 10rem;
					text-align: center;
					h3 {
						span {
							font-weight: bold; } } } } } } }

#portfolio {
	.container {
		.row {
			.twelve {
				.grid-portfolio {
					display: grid;
					grid-template-rows: auto;
					grid-template-columns: repeat(auto-fill, minmax(333px, 1fr));
					align-items: center;
					grid-gap: 20px;
					img {
						display: block;
						width: 100%;
						height: auto; } } } } } }
/* Main blog */
#main-blog {
	.blog-head {
		margin: 5rem 0;
		img {
			display: block;
			width: 100%;
			height: auto; } } }




#contact {
	.container {
		.row {
			.twelve {
				form {
					.form-group {}
					textarea {
						height: 20%;
						overflow: auto; }
					input[type="button"] {
						display: block; } } } } } }

#blog {
	.container {
		.row {
			.twelve {
				#blog-grid {
					display: grid;
					grid-template-rows: auto;
					grid-template-columns: repeat(auto-fill, minmax(270px, 1fr));
					grid-gap: 20px;
					margin-bottom: 5rem;
					.post {
						text-align: center;
						.card {
							.blog-image {
								img {
									display: block;
									width: 100%;
									height: auto; } }
							.blog-lead {
								position: relative;
								p {
									font-weight: bold;
									font-size: 1.8rem;
									margin: 2rem 0 0; }
								div {
									height: 5px;
									width: 50px;
									background: red;
									position: absolute;
									left: 50%;
									transform: translateX(-50%);
									margin: 4rem 0; } }
							.blog-info {
								margin-top: 5rem;
								text-align: center; } } } } } } } }


#article {
	.container {
		.row {
			.twelve {
				h1.article-lead {
					margin: 5rem 0; } } } } }



footer {
	margin: 3rem 0;
	.container {
		.row {
			.six {
				&:nth-child(2) {
					text-align: right; }
				.social-links {
					a {
						margin-left: 1rem;
						color: $links-color;
						text-decoration: none;
						&:hover {
							color: $links-color-hover; } } } } } } }


/* fadeIn text */

@-webkit-keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0; }

  100% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1; } }


@keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
    filter: blur(12px);
    opacity: 0; }

  100% {
    -webkit-filter: blur(0px);
    filter: blur(0px);
    opacity: 1; } }

.text-focus-in {
  animation: text-focus-in 2s cubic-bezier(0.55, 0.085, 0.68, 0.53) both; }

/* Responsive stuffs */
@media (max-width: 768px) {
	#header {
		margin: 0px;
		padding: 25px 0;
		border-bottom: 1px solid #f1f1f1;
		#main-header {
			display: block;
			height: 100%;
			position: relative;
			label {
				display: block;
				position: absolute;
				left: 95%;
				cursor: pointer;
				line-height: 2; }
			#toogle {
				&:checked + nav {
					display: block; } }
			nav#main-menu {
				display: none;
				float: none;
				ul {
					margin-top: 55px;
					background: #fafafa;
					li {
						display: block;
						margin-left: 0;
						a {
							display: block;
							padding: 10px;
							font-weight: bold;
							border-bottom: 1px solid #999; } } } } } }
	#main {
		.work {
			.columns {
				.grid {
					.image1 {
						grid-row: 1 / auto;
						grid-column: 1 / 7; }
					.image2 {
						grid-row: 2 / auto;
						grid-column: 1 / 7; }
					.image3 {
						grid-row: 3 / auto;
						grid-column: 1 / 7; } } } } } }





@media (max-width: 550px) {
	footer {
		.row {
			.twelve {
				.copyright {
					text-align: center; }
				.social-links {
					text-align: center; } } } } }


@media (max-width: 460px) {
	#contact {
		.container {
			.row {
				.twelve {
					form {
						.form-group {}
						textarea {
							height: 20%;
							width: 300px;
							overflow: auto; }
						input[type="button"] {
							display: block; } } } } } } }
